html,
body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-size: 62.5%;
}
body {
}
.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 3rem auto;
  width: 80vw;
  background-color: white;
  flex-wrap: wrap;
}
.item-value {
  font-size: 3rem;
}
.item-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  color: white;
  padding: 1rem;
  margin: 1rem auto;
  text-transform: uppercase;
  list-style: none;
  background-color: #333;
}
